<template>
  <div class="main-container">
    <div class="nav">
      <div class="logo">
        <h1><router-link to="/">风云珏玉</router-link></h1>
      </div>
      <div class="menu">
        <ul class="nav-menu">
          <li><router-link to="/resume">个人简历</router-link></li>
          <li class="active"><router-link to="/travel">我的旅行</router-link></li>
        </ul>
      </div>
    </div>
    <div class="travel-map">
      <div class="title">旅行足迹</div>
      <div id="MainMap"></div>
    </div>
    <!--弹窗-->
    <div id="feedbackBg" class="feedback-bg" v-on:click="cancelDialog();"></div>
    <div id="feedbackDialog" class="feedback-dialog">
        <div class="title">我的旅游计划</div>
        <div id="feedbackInfo">
            <div class="prov">旅游地点：{{travelProv}}</div>
            <div class="prov">旅游时间：<input type='date' v-model="travelTime" /></div>
            <textarea v-model="travelRemark" placehodler="请填写旅游备注">{{travelRemark}}</textarea>
            <div class="option-btn">
                    <button class="btn btn-sure" v-on:click="submitTravel()">提交</button>
                    <button class="btn btn-cancel" v-on:click="cancelDialog();">取消</button>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
var map,myChart;
export default {
  name: 'Travel',
  data () {
    return {
      msg: 'Travel',
      travelRemark:'',
      travelProv: '',
      provIndex:'',
      travelTime:''
    }
  },
  mounted(){
      myChart = echarts.init(document.getElementById('MainMap'));
      var option = {
         visualMap: {
            min: 0,
            max: 1,
            left: 'left',
            top: 'bottom',
            text: ['高', '低'],
            calculable: false,
            orient: 'horizontal',
            show: false,
            inRange: {
                color: ['#e0ffff', '#006edd'],
                symbolSize: [30, 100]
            }
        },
        tooltip: {
            trigger: 'item',
            formatter: function (datas) {
              var data = datas.data;
              var res = data.name + '<br/>';
              if(data.value==1){
                res+="旅行时间：" + data.tDate + '<br/>';
                res+="旅行备注：" + data.remarks;
              }
               return res
             }
        },
        series: [
            {
                name: '中国',
                type: 'map',
                mapType: 'china',
                selectedMode : 'single',
                label: {
                    normal: {
                        show: true
                    },
                    emphasis: {
                        show: true
                    }
                },
                data:[
                    {name:'黑龙江', value:0},
                    {name:'吉林', value:0},
                    {name:'辽宁', value:0},
                    {name:'内蒙古', value:0},
                    {name:'河北', value:0},
                    {name:'北京', value:0},
                    {name:'天津', value:0},
                    {name:'山东', value:0},
                    {name:'江苏', value:0},
                    {name:'上海', value:0},
                    {name:'安徽', value:0},
                    {name:'浙江', value:0},
                    {name:'福建', value:0},
                    {name:'江西', value:0},
                    {name:'台湾', value:0},
                    {name:'湖北', value:1, tDate:'2018-09-30', remarks:'目前在武汉'},
                    {name:'河南', value:1, tDate:'2018-09-12', remarks:'河南省是粮食大省'},
                    {name:'山西', value:0},
                    {name:'陕西', value:0},
                    {name:'甘肃', value:0},
                    {name:'宁夏', value:0},
                    {name:'青海', value:0},
                    {name:'新疆', value:0},
                    {name:'西藏', value:0},
                    {name:'四川', value:0},
                    {name:'重庆', value:0},
                    {name:'贵州', value:0},
                    {name:'湖南', value:0},
                    {name:'广西', value:0},
                    {name:'广东', value:0},
                    {name:'云南', value:0},
                    {name:'海南', value:1, tDate:'2018-09-02', remarks:'旅游大省'},
                    {name:'香港', value:0},
                    {name:'澳门', value:0}
                ]
            }
        ]
    };

    myChart.setOption(option);

    var _this = this;
    
    myChart.on('click', function (params) {
        var data = params.data;
        console.log(params);
        if(data.value==0){
          _this.provIndex = params.dataIndex;
          _this.travelProv = data.name;
          _this.showDialog();
        }
    });

  },
  methods:{
    cancelDialog(){
      document.getElementById('feedbackBg').style.display='none';
      document.getElementById('feedbackDialog').style.display='none';
    },
    showDialog(){
      document.getElementById('feedbackBg').style.display='block';
      document.getElementById('feedbackDialog').style.display='block';
    },
    submitTravel(){
      alert('旅游计划添加成功！');
      this.cancelDialog();
      var option = myChart.getOption();
      option.series[0].data[this.provIndex].value=1;
      option.series[0].data[this.provIndex].tDate=this.travelTime;
      option.series[0].data[this.provIndex].remarks=this.travelRemark;
      myChart.setOption(option);
    }
  }
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../../static/css/travel.css'
</style>



