import Vue from 'vue'
import Router from 'vue-router'
import GateWay from '@/components/GateWay'
import Main from '@/components/Main'
import Travel from '@/components/Travel'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'GateWay',
      component: GateWay
    },
    {
      path: '/resume',
      name: 'Main',
      component: Main
    },
    {
      path: '/travel',
      name: 'Travel',
      component: Travel
    }
  ]
})



